
.Massege {
    background-color: #61dafb21;
border-radius: 20px;
}

.writeMassage div {
    display: inline-flex;
}

.buttone button {
    display: block;
    border-radius: 25%;

}
.buttone button:hover{
    box-shadow: 0 3px 8px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
    transition-duration: 0.2s;
background-color: #0f2bdb;
}

.oneMessage span {

    display: block;
    font-weight: bold;
    font-size: 17px;
    padding: 5px;
    font-style: oblique;
    font-family: Gadugi;
    color: #020052;

}
.oneMessage span:hover{
    color: black;
    background-color:#00020307;

}